
import { defineComponent,ref } from "vue";
export default defineComponent({
  name: "rank-data",
  props: {
    rankData: {
      type: Array,
      default: () => {
        return [];
      },
    },
  },
  setup(props) {
    function getItemClass(index:number) {
      return index < 3 ? "yellow" : "blue";
    }
    const chartData=ref(props.rankData);
    return {
      getItemClass,
      chartData
    };
  },
});
